.container {
    display: flex;
    flex-direction: column;
    width: 95%;
    margin: 0 auto;
}

.title {
    color: #2B3445;
}

.tableHeader {
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    font-weight: 600;
    line-height: 1.5;
    color: #7D879C;
    padding-bottom: 16px;
    padding-left: 20px;
    flex-wrap: wrap;
}

.tableHeaderItem {
    /*padding-right: 46px;*/
    text-align: left;
}

.tableHeaderStatus {
    padding-right: 36px;
    text-align: left;
    padding-left: 16px;
}

.tableHeaderDate {
    padding-right: 46px;
    text-align: left;
    padding-left: 26px;
}

.orderWrapper {
    /*flex: 1 1 0;*/
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 8px;
    padding: 16px;
    box-shadow: rgb(3 0 71 / 9%) 0px 1px 3px;
    margin-bottom: 10px;
    color: #2B3445;
    font-size: 14px;
    font-weight: 400;
}

.item div {
    font-weight: 400;
    padding-bottom: 10px;
    color: #7D879C;
}

.amountCart {
    color: #2B3445;
}

.emptyCart{
    height: 40px;
    background-color: #ffffff;
    border-radius: 8px;
    padding: 16px;
    box-shadow: rgb(3 0 71 / 9%) 0px 1px 3px;
    margin-bottom: 10px;
    color: #2B3445;
    font-size: 14px;
    font-weight: 400;
}